<template>
    <div class="flash-wrap">
        <ul class="flash-dd clearfix">
            <li class="fl" v-for="(n,index) in goodsClass" :key="n.id" @click="link(n)">
                    <img v-lazy="URL + n.pic_url">
                <p>{{n.class_name}}</p>
            </li>
            <!-- <li class="fl" @click="btn">
                <img v-lazy="URL + goodsClass.pic_url">
                <p>{{text}}</p>
            </li> -->
        </ul>
        <div class="flash-dt clearfix">
            <div class="flash-news fl text-center">公告</div>
            <div class="flash-content fl">
                <em></em>
                <ul class="scroll_box" ref="scroll_box">
                    <li  class="text-single-hidden" v-for="(item,index) in data" :key="item.id">{{item.title}}</li>
                </ul>
            </div>
            <router-link to="/Notice" class="flash-more fr">更多</router-link>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'newsflash',
        data() {
            return {
                data:'',
                // text: '积分商城'
            }
        },
        props:{
            goodsClass:null
        },
        methods: {
            link(n){
               this.$router.push({
                        name:'list',
                        params:{
                            id:n.id,
                            status:'nav'
                        }
                    });
            },
            btn() {
                this.$router.push({name: 'IntegralMall'})
            }
        }
    }
</script>
<style lang="less" scoped>
    .flash-wrap{
        background:#fff;
        padding: .2rem;
        .flash-dt{
            width:100%;
            height:.59rem;
            padding:.1rem 0;
            border-bottom:1px solid #eaf0f4;
            background:#fff;
            .flash-news{
                width:1.3rem;
                height:100%;
                position:relative;
                border-right:2px dotted #ccc;
                line-height: .59rem;
                font-size:.32rem;
                color:#111;
            }
            .flash-content{
                position: relative;
                width:4.5rem;
                height:100%;
                padding:0 .2rem;
                line-height:.59rem;
                font-size:.28rem;
                color:#333;
                white-space:nowrap;
                overflow:hidden;
                text-overflow:ellipsis;
                border-right:1px dotted #ccc;
                em{
                    display:inline-block;
                    width: .28rem;
                    height: .28rem;
                    background:url(../../../assets/icon1.png) no-repeat;
                    background-size:100% 100%;
                    margin-right:.2rem;
                    margin-top: .16rem;
                }
                .scroll_box{
                    width: 4.5rem;
                    height: .59rem;
                    position: absolute;
                    left:.7rem;
                    top:-.1rem;
                    transition: 1s all ease;
                    li{
                        font-size: .2rem;
                        line-height: .59rem;
                    }
                }
            }
            .flash-more{
                width:.8rem;
                height:.59rem;
                text-align:center;
                line-height:.59rem;
                font-size:.28rem;
                color:#797979;
            }
        }
        .flash-dd{
            width:100%;
            li{
                width:20%;
                text-align:center;
                img{
                    width:1.15rem;
                    -webkit-border-radius:50%;
                    border-radius:50%;
                    height:1.15rem;
                }
                p{
                    // font-family: 'NotoSansHans-Regular';
                    font-size:.25rem;
                    color:#403f3f;
                    font-weight: 600;
                    padding:.2rem 0;
                    line-height:100%;
                }
            }
            li:nth-child(n+6){
                margin-top:.2rem;
            }
        }
        .container-main{
            width:100%;
            height:2.33rem;
            background:#fff;
        }
    }
</style>



