<template>
    <div>
        <div class="hotGoods-wrap" v-for="(item,index) in goods" :key="item.id">
            <div class="hd">
                <div class="title fl">
                    <span class="fl">{{item.class_name}}</span>
                    <!-- <img class="fl title-img" :src="titleImg[index]" alt=""> -->
                    <img class="fl title-img" :src="URL+titleImg[index]" alt="">
                </div>
                <span @click="more(item)" class="more fr">更多<em><i></i><b></b></em></span>
            </div>
            <ul class="content-main clearfix">
                <li class="fl" v-for="(d,index) in item.goods" :key="d.id"  @click="tolink(d)">
                    <img v-lazy="URL + d.pic_url">
                    <p class="desi text2-hidden">{{d.title}}</p>
                    <div class="price_market clearfix">
                        <p class="stock clearfix"> <span class="market fl">￥{{d.price_market}}</span></p><p class="price fl">￥<span>{{d.price_member}}</span>
                        </p>
                        <!-- <span  @click.stop="join(d)" class="cart" ></span> -->
                        <span v-if="d.is_customs == 0"  @click.stop="join(d)" class="cart" ></span>
                    </div>
                </li>
            </ul>
        </div>      
    </div>
</template>
<script>
    import qs from 'qs';
    import { Toast } from 'mint-ui';  
    export default {
        name : 'hotGoods',
        data(){
            return {
                mark:{},
                titleImg:[
                    // require('@/assets/clean-1.png'),
                    // require('@/assets/kitchen-2.png'),
                    // require('@/assets/fresh-3.png'),
                    // require('@/assets/food-4.png'),
                    // require('@/assets/wine-5.png'),
                    // require('@/assets/dress-up-6.png'),
                    // require('@/assets/furniture-7.png'),
                    // require('@/assets/customized-8.png'),
                    // require('@/assets/toys-9.png'),
                    // require('@/assets/appliance-10.png')
                ]
            }
        },
        props:{
            goods:null
        },
        created(){
            this.getMark();
            this.getImg();
        },
        methods:{
            getMark(){
                 this.axios({
                    url:API_URL + 'Home/Index/getGoodsMarking',
                    method:'get',
                }).then((res) => {
                    this.mark = res.data.data;
                }).catch((err) => {
                    console.log(err);
                });
            },
            getImg(){
                 this.axios({
                    url:API_URL + 'Home/Index/getClassImg',
                    method:'get',
                }).then((res) => {
                    this.titleImg = res.data.data;
                }).catch((err) => {
                    console.log(err);
                });
            },
            more(data){
                this.$router.push({
                    name:'list',
                    params:{
                        id:data.id,
                        status:'nav'
                    }
                });
            },
            tolink(data){
                this.$router.push({
                    name:'product',
                    params:{
                        id:data.id,
                        status:1
                    }
                });
            },
            //加入购物车
            join(info){

                 if(info.stock == 0){
                    Toast({
                        message: '库存不足',
                        position: 'middle',
                        duration: 800
                        });
                        return false
                }else if(!sessionStorage.getItem('user_ID')){
                    this.$router.push('/LogoIn');
                    return;
                };
                this.axios.post(API_URL + 'Home/Cart/add_cart',qs.stringify({
                    app_user_id:sessionStorage.getItem('user_ID'),
                    goods_id:info.id,
                    goods_num:1,
                    price_new:info.price_member
                })).then((res) => {
                    if(res.data.status == 1){
                        Toast({
                            message: res.data.msg,
                            position: 'middle',
                            duration: 800
                        });
                    }else{
                       Toast({
                            message: res.data.msg,
                            position: 'middle',
                            duration: 800
                        }); 
                    }
                }).catch((err) => {
                    console.log(err);
                });
            }
        }
    }
</script>
<style lang="less" scoped>
    .hotGoods-wrap{
        background:#fff;
        .hd{
            overflow: hidden;
            position: relative;
            // border-bottom:0.5px solid #d0d0d0;
            margin:.2rem .4rem;
            .title{
                    height: .55rem;
                    line-height: .55rem;
                span{
                    font-size:.32rem;
                    font-weight: 600;
                    color:#333;
                }
                .title-img{
                    padding-left: .1rem;
                    width: .45rem;
                    height: .5rem;
                    
                }
            }
            
            .more{
                line-height:.55rem;
                color:#999;
                font-size:.28rem;
                position:relative;
                padding-right:.2rem;
                em{
                    position:absolute;
                    right:0;
                    top:.2rem;
                    i{
                        border-left:.08rem solid #ccc;
                        border-top:.08rem solid transparent;
                        border-bottom:.08rem solid transparent;
                        position:absolute;
                        right:.05rem;
                        top:0;
                    }
                    b{
                        border-left:.08rem solid #fff;
                        border-top:.08rem solid transparent;
                        border-bottom:.08rem solid transparent;
                        position:absolute;
                        right:.08rem;
                        top:0;
                    }
                }
            }
        }
        .hd{
            background-image: -webkit-linear-gradient(top, #d0d0d0, #d0d0d0 50%, transparent 50%);
            background-image: linear-gradient(180deg, #d0d0d0, #d0d0d0 50%, transparent 50%);
            background-size: 120% 1px;
            background-repeat: no-repeat;
            background-position: bottom left;
            background-origin: content-box;
        }
        .content-main{
            margin: 0 .15rem;
            overflow:hidden;
            li{
                width:32%;
                background:#fff;
                margin-top:.1rem;
                margin-right: .08rem;
                img{
                    width: 2.3rem;
                    height: 2.3rem;
                    margin-bottom: .15rem;
                }
                .desi{
                    font-family: 'NotoSansHans-Regular';
                    font-size:.24rem;
                    color:#404040;
                    line-height:.35rem;
                    padding: 0 .1rem;
                    height:.7rem;
                    box-sizing: border-box;
                }
                .price_market {
                    padding:.1rem;
                    padding-top:.2rem;
                    position: relative;
                    .market {
                        text-decoration:line-through;
                        font-size: .24rem;
                        color: #aeadad;
                    }
                   .price{
                        color:#e02828;
                        font-size:.28rem;
                        text-indent:-.05rem;
                        span{
                            font-size:.28rem;
                            font-weight: 600;
                        }
                    } 
                    
                    .cart {
                        position: absolute;
                        right: .2rem;
                        top: .19rem;
                        width:.53rem;
                        height:.55rem;
                        background:url(../../../assets/buy-cart-12.png) no-repeat;
                        background-size:100% 100%;
                    }
                }
            }
            li:nth-child(3n){
                margin-right: 0;
            }
        }
    }
</style>


