<template>
	<div class="market-home">
		<home-header></home-header>
		<div id="mescroll" class="mescroll" ref="div">
			<mt-swipe :auto="4000">
				<mt-swipe-item v-for="(item,index) in banner" :key="item.id">
					<img v-lazy="URL + item.pic_url" @click="toGoods(item.gid)">
				</mt-swipe-item>
			</mt-swipe>
			<news-flash :goodsClass="nav_class"></news-flash>
			<hot-goods :goods="hot_goods"></hot-goods>
			<to-Top></to-Top>
		</div>
	</div>
</template>

<script>
	import HomeHeader from './children/header';
	import hotGoods from './children/hotGoods';
	import NewsFlash from './children/newsFlash';
	import toTop from './children/toTop';
	import qs from 'qs';
	export default {
		name: 'marketHome',
		data() {
			return {
				continuous: true,
				defaultIndex: 0,
				showIndicators: true,
				prevent: true,
				stopPropagation: true,
				banner: [],
				nav_class: [],
				hot_goods: [],
				position: false
			}
		},
		updated() {
			this.$nextTick(function() {
				  this.position = this.$store.state.changeHomeScrollY ? false : true;
                if (this.position === true) return;
				this.$refs.div.scrollTop = this.$store.state.changeHomeScrollY;
			})
		},
		beforeRouteLeave(to, from, next) {
			let position = this.$refs.div.scrollTop; //记录离开页面的位置
			this.$store.state.changeHomeScrollY = position; //离开路由时把位置存起来
			this.$store.commit('changeRecruitScrollY', ''); //离开路由时把位置清空
			this.$store.state.listData = [];
			this.$store.state.listInfo = {};
			this.$store.state.groupScrollY = 0;
			this.$store.state.groupData = [];
			sessionStorage.removeItem('classId');
			next()
		},
		mounted() {
			//创建MeScroll对象
			var self = this;
			self.mescroll = new MeScroll("mescroll", {
				//下拉刷新
				down: {
					callback: self.downCallback, //下拉刷新的回调,别写成downCallback(),多了括号就自动执行方法了
					use: self.down, //是否初始化下拉刷新; 默认true
					auto: false, //是否在初始化完毕之后自动执行下拉回调callback; 默认true
					autoShowLoading: false, //如果在初始化完毕之后自动执行下拉回调,是否显示下拉刷新进度; 默认false
					isLock: false, //是否锁定下拉,默认false;
					isBoth: false, //下拉刷新时,如果滑动到列表底部是否可以同时触发上拉加载;默认false,两者不可同时触发;
					offset: 60, //触发刷新的距离,默认80
					outOffsetRate: 0.2, //超过指定距离范围外时,改变下拉区域高度比例;值小于1且越接近0,越往下拉高度变化越小;
					bottomOffset: 20, //当手指touchmove位置在距离body底部20px范围内的时候结束上拉刷新,避免Webview嵌套导致touchend事件不执行
					minAngle: 45, //向下滑动最少偏移的角度,取值区间  [0,90];默认45度,即向下滑动的角度大于45度则触发下拉;而小于45度,将不触发下拉,避免与左右滑动的轮播等组件冲突;
					hardwareClass: "mescroll-hardware", //硬件加速样式;解决iOS下拉因隐藏进度条而闪屏的问题,参见mescroll.css
					warpId: null, //可配置下拉刷新的布局添加到指定id的div;默认不配置,默认添加到mescrollId
					warpClass: "mescroll-downwarp", //容器,装载布局内容,参见mescroll.css
					resetClass: "mescroll-downwarp-reset", //高度重置的动画,参见mescroll.css
				},
			});
		},
		created() {
			//是否直接登录
			if (!sessionStorage.getItem('user_ID')) {
				let userid = localStorage.getItem('user_ID');
				if (userid) {
					sessionStorage.setItem('user_ID', userid);
				} else {
					// this.$router.push('/LogoIn');return false;
				}
			}
			this.getGoodsData();
		},
		methods: {
			getGoodsData() {
				this.axios.post(API_URL + 'Home/Supermarket/index', qs.stringify({
					app_user_id: sessionStorage.getItem('user_ID'),
				})).then((res) => {
					if (res.data.status == 1) {
						setTimeout(() => {
							this.mescroll.endSuccess(); //无参
						}, 200);
						this.banner = res.data.data.banner;
						this.nav_class = res.data.data.goodsclass;
						this.hot_goods = res.data.data.goods;
					}
				}).catch((err) => {
					console.log(err);
				});
			},
			downCallback() {
				this.$store.state.changeHomeScrollY = '';
				this.$refs.div.scrollTop = 0;
				this.$store.state.listData = [];
				this.$store.state.listInfo = {};
				this.getGoodsData();
			},
			// 广告跳转
			toGoods(gid) {
				if (gid) {
					this.$router.push({
						name: 'product',
						params: {
							id: gid,
							status: 1
						}
					});
				}

			},
		},
		components: {
			HomeHeader,
			hotGoods,
			NewsFlash,
			toTop
		}
	}
</script>

<style lang="less" scoped>
	.market-home {
		background: #fff;
		.mint-swipe {
			height: 3.6rem;
			padding: 0 .1rem;
			img {
				border-radius: .15rem;
				width: 100%;
				height: 100%;
			}
			.mint-swipe-indicators {
				width: 100%;
				text-align: center;
				.mint-swipe-indicator {
					width: 20px;
					height: 20px;
				}
			}
		}
	}
</style>



